<template>
    <div>
        <div>

            <div style="display:flex;justify-content: center; flex-wrap: wrap;">
                <div v-for="(item,index) in onlineVideos" :key="index">
                    <el-card class="card-style"
                             :title="'点击前往 ' + item.name">
                        <el-link :href="item.url" target="_blank">
                            <div style="display: flex;">
                                <div>
                                    <img :src="item.icon" style="border-radius: 50px" width="50" alt="">
                                </div>
                                <div style="margin-left: 6px">
                                    <div style="display: flex">
                                        <span style="color: darkmagenta;font-family: 华文宋体">
                                            {{ item.name }}
                                        </span>
                                        <span>
                                            <span v-for="(itemTag,indexTag) in item.label.toString().split(';')" :key="indexTag">
                                            <el-tag :type="getType(indexTag)" style="margin-left: 3px">{{
                                                    itemTag
                                                }}</el-tag>
                                                </span>
                                        </span>
                                    </div>
                                    <div style="margin-top: 4px;">
                                        {{ item.remark }}
                                    </div>
                                </div>
                            </div>
                        </el-link>
                    </el-card>
                </div>
            </div>
        </div>
    </div>
</template>

<script>

    export default {
        name: "OnlineVideo",
        data: () => ({
            onlineVideos:[],//资源列表
        }),
        mounted() {
            this.initOnlineVideos();
        },
        methods: {
            /**
             * 初始化资源列表
             */
            initOnlineVideos(){
              this.onlineVideos =  require('./onlineVideo.json');
            },
            /**
             * 返回标签类型
             * @param index
             */
            getType(index) {
                switch (index) {
                    case 0:
                        return 'warning';
                    case 1:
                        return 'danger';
                    case 2:
                        return 'success';
                    default:
                        return 'info'
                }
            }
        }
    }
</script>

<style scoped>
    .card-style{
        background-color: rgba(255,255,255,0.2);
        min-width: 300px;
        min-height: 100px;
        max-width: 300px;
        margin:10px 10px 10px auto;
        margin-top: 18px;
        border-radius: 10px
    }
</style>